import axios from 'axios';

/* 分站列表 */
export function WebSite(callback, type, productNum) {
    axios({
        method: "GET",
        headers: {
          "Content-Type": "application/x-www-form-urlencoded",
        },
        url: "/giftmall/webSite?page=0&size=1000"
    }).then(function(res) {
        res = res.data;
        if (res.code == 200) {
            res.data = res.data || {};
            res.data.list = res.data.list || [];
            let List = res.data.list.map(item => {
                item = item || {};
                let obj = {};
                obj.value = item.name;
                obj.id = item.id;
                return obj
            })
            callback(List, res.data)
        }
    });
}


