<template>
<div>
	<el-form label-width="80px" style="margin-top:15px;">
		 <el-row>
			<el-col :span="6">
					<el-form-item label="文章标题">
							<el-input v-model="form.title"></el-input>
				  </el-form-item>
			</el-col>
		 </el-row>
		<el-row>
			<el-col :span="6">
				<el-form-item label="文章类型">
				   <el-select v-model="form.type" >
						<el-option label="系统公告" value="1"></el-option>
						<el-option label="常见问题" value="2"></el-option>
					</el-select>
				</el-form-item>	
			</el-col>
		</el-row>
		<el-row>
			<el-col :span="4">
					<el-form-item label="文章来源">
							<el-input v-model="form.author"></el-input>
				  </el-form-item>
			</el-col>

		</el-row>

		<el-form-item label="文章详情">
				<quill-editor  style="height:250px;width:100%;margin-bottom:50px"
            v-model="form.content"
            ref="myQuillEditor"
						:options="editorOptions"
            @blur="onEditorBlur($event)" @focus="onEditorFocus($event)"
            @change="onEditorChange($event)">
        </quill-editor>
				<el-button type="primary" @click="send" style="margin-top:70px">立即提交</el-button>
		</el-form-item>
			<el-upload
					id="uploadImg"
					style="visibility: hidden"
					action="/"
					list-type="picture-card"
					:auto-upload="false"
					:on-change="fileChangeImg"
					:on-preview="handlePictureCardPreview"
					:on-remove="handleRemove">
					<i class="el-icon-plus"></i>
				</el-upload>
	</el-form>
</div>
</template>

<script>
var that = ''
	export default {
		data() {
			return {
				dialogImageUrl: '',
				fileList:[],
        dialogVisible: false,
				form: {},
				editorOptions: {
					placeholder: '请输入',
					theme: 'snow',
					modules: {
						toolbar: {
							container: [
              ['bold', 'italic', 'underline', 'strike'],
              ['blockquote'],
              [{ 'header': 1 }, { 'header': 2 }],
              [{ 'list': 'ordered' }, { 'list': 'bullet' }],
              [{ 'script': 'sub' }, { 'script': 'super' }],
              [{ 'indent': '-1' }, { 'indent': '+1' }],
              [{ 'direction': 'rtl' }],
              [{ 'header': [1, 2, 3, 4, 5, 6, false] }],
              [{ 'font': [] }],
              [{ 'color': [] }, { 'background': [] }],
              [{ 'align': [] }],
              ['clean'],
              ['link', 'image']
            ],
							handlers: {
								image: function(value) {
									if(value) {
										 document.querySelector('#uploadImg input').click()
									} else {
										this.quill.format('image', false);
									}
								}
							}
						}
					}
				}
			}
		},
		computed:{
			 editor() {
            return this.$refs.myQuillEditor.quill;
        },
		},
		mounted(){
			that = this
			if(this.$route.query.id){
				this.getDetail();
			}
		},
		methods: {
			onEditorReady(editor) { // 准备编辑器
			},
			onEditorBlur(){}, // 失去焦点事件
			onEditorFocus(){}, // 获得焦点事件
			onEditorChange(){}, // 内容改变事件
			saveHtml:function(event){
				alert(this.content);
			},
			getDetail(){
				let that=this;
				this.axios.get(
						"/notice/"+that.$route.query.id,
						{
							headers: {
								"Content-Type": "application/x-www-form-urlencoded"
							}
						}
					)
					.then(function(response) {
						if (response.data.code == 200) {
						    that.form = {};
							that.form.title=response.data.data.title;
							that.form.author=response.data.data.author;
							that.form.content=response.data.data.content;
							that.form.type=response.data.data.type;
							console.log(that.form);
						}
					});
			},
			fileChange(file, fileList) {
				let that=this;
				this.FileChange(file, res => {
					console.log(res.url);
					that.fileList.push({name:"1",url:res.url})
				});
			},
			fileChangeImg(file) {
				this.FileChange(file, res => {
					let length = this.editor.getSelection().index
        	this.editor.insertEmbed(length, 'image', res.url)
        	this.editor.setSelection(length + 1)
				})
			},
			FileChange(e, callback) {
				if (!e) return;
				let file = e.raw;
				if (!file) return;
				let param = new FormData();
				param.append('chunk', '0');
				param.append('chunks', '1');
				param.append('file', file, file.name);
				let config = {
					headers: {
						'Content-Type': 'multipart/form-data'
					}
				};
				this.axios({
							method: "GET",
							headers: {
								"Content-Type": "multipart/form-data"
							},
							url: "/qiniu/token"
						}).then(res => {
					let token = res.data.data;
					param.append('token', token);
					this.Uploading(param, config, callback);
					return;
				})
			},
			Uploading(param, config, callback) {
				this.axios.post('http://upload-z1.qiniup.com/', param, config).then(res => {
					let params = {
						message: 'ok',
						url: `http://qiniu.tianyalei.com/${res.data.key}`
					}
					if (callback) callback(params)
				}).catch(err => {
					console.log(err, 'pppp')
				})
			},
			handleRemove(file, fileList) {
				console.log(file);
				for(var i=0;i<this.fileList.length;i++){
						if(file.url==this.fileList[i].url){
							 this.fileList.splice(i,1)
						}
				}
      },
      handlePictureCardPreview(file) {
        this.dialogImageUrl = file.url;
        this.dialogVisible = true;
		},
		send(){
                let that=this; 
                let method = 'post';
				if(that.$route.query.id){
                    that.form.id=that.$route.query.id;
                    method = 'put';
				}else{
                    delete that.form.id
				}
				let newary=[];
				for(var i=0;i<that.fileList.length;i++){
                    newary.push(that.fileList[i].url)
				}
				that.form.images=newary.join(',');
                this.axios[method]('/giftmall/notice',  this.qs.stringify(that.form), {
                    headers: {
                        'Content-Type':'application/x-www-form-urlencoded'
                    }
                }).then(function (response) {
                    if(response.data.code==200){
                        that.$message({
                            message:'提交成功',
                            type: 'success'
                        });
                        that.$router.push({path:'/articleList'})
                    }
                })
            }
		}
	}

</script>
<style>
.ql-snow .ql-picker-label::before{
	  position: relative;
    top: -5px;
}
</style>


