<template>
	<section> 
		<el-form class="toolbar" label-width="80px" style="margin-top:10px;padding-top:20px;">
            <el-row>
                <el-col :span="4">
                    <el-select v-model="webSiteId" placeholder="全部">
                        <el-option
                            v-for="item in webOption"
                            :key="item.id"
                            :label="item.value"
                            :value="item.id"
                        />
                    </el-select>
                </el-col>
                <el-col :span="6">
                    <el-form-item label="日期">
                        <el-date-picker
                            v-model="dateValue"
                            type="daterange"
                            @change="datechange"
                            style="width:100%"
                            range-separator="至"
                            value-format="yyyy-MM-dd"
                            start-placeholder="开始日期"
                            end-placeholder="结束日期">
                        </el-date-picker>
                    </el-form-item> 
                </el-col>
                <el-col :span="2">
                    <el-form-item>
                        <el-button type="primary" @click="shousuo">搜索</el-button> 
                    </el-form-item>
                </el-col>
            </el-row>
		</el-form>
		<el-table highlight-current-row :data="dataList" style="width: 100%;">
        <el-table-column align="center" prop="webSiteName" label="分站名称" ></el-table-column>
        <el-table-column align="center" prop="bonus" label="分站奖金" >
            <template slot-scope="scope">
                {{scope.row.bonus/100}} 元
            </template>
        </el-table-column>
        <el-table-column align="center" prop="totalBonus" label="分站累计奖金" >
            <template slot-scope="scope">
                {{scope.row.totalBonus/100}} 元
            </template>
        </el-table-column>
		<el-table-column align="center" prop="totalUserCount" label="总用户数" ></el-table-column>
		<el-table-column align="center" prop="newUserCount" label="新用户数" ></el-table-column>
		<el-table-column align="center" prop="payUserCount" label="下单用户数" ></el-table-column>
		<el-table-column align="center" prop="totalTaskCount" label="总任务数" ></el-table-column>
		<el-table-column align="center" prop="totalPackCount" label="总包裹数" ></el-table-column>
		<el-table-column align="center" prop="day" label="日期" ></el-table-column>
    </el-table>
			
    <div style="padding:20px;text-align:center;">
        <el-pagination
            background
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="page"
            :page-sizes="[10, 20, 30, 40]"
            :page-size="size"
            layout="total, sizes, prev, pager, next, jumper"
            :total="total">
        </el-pagination>
    </div> 

	</section>
</template>

<script>
import {gettime} from "../../utils/formData";  
import {WebSite} from "../../utils/jsonData";  
export default {
    data() {
        return {
            total:0,
            page: 1,
            size: 10,
            dataList: [],
            webOption: WebSite((res) => this.webOption = res),
            webSiteId: '',
            dateValue:'',
            begin:'',
            end:'',
            options: null,
        };
    },
    methods: {
        shousuo: function() {
            let that = this;
            this.axios({
                method: "GET",
                headers: {
                    "Content-Type": "application/x-www-form-urlencoded"
                },
                url: "/Daily?page="+(that.page-1)+"&size="+that.size+"&webSiteId="+that.webSiteId
            }).then(function(response) {
                if (response.data.code == 200) {
                    that.dataList = response.data.data.list;
                    that.total=response.data.data.totalCount;
                    for(var i=0;i<that.dataList.length;i++){
                        that.dataList[i].day=gettime(that.dataList[i].day)
                    }
                }
            });
        },
        datechange(e){
            this.begin=e.split('至')[0];
            this.end=e.split('至')[1];
        },
        handleSizeChange(val) {
            console.log(`每页 ${val} 条`);
            this.size=val;
            this.shousuo();
        },
        handleCurrentChange(val) {
            console.log(`当前页: ${val}`);
            this.page=val;
            this.shousuo();
        }    
    },
    created() {
        this.shousuo();
    },
};
</script>


