<template>
  <section>
    <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
      <el-form :inline="true">
        <el-form-item label="文章标题">
          <el-input v-model="title" placeholder="文章标题"></el-input>
        </el-form-item>
        <el-form-item label="文章类型">
          <el-select v-model="type" placeholder="文章类型">
            <el-option label="系统公告" value="1"></el-option>
            <el-option label="常见问题" value="2"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="shousuo">搜索</el-button>
        </el-form-item>
      </el-form>
    </el-col>
      <el-table highlight-current-row :data="dataList" style="width: 100%;">
          <el-table-column label="id" >
              <template align="center" slot-scope="scope">
                  {{scope.row.id}}
              </template>
          </el-table-column>
        <el-table-column label="文章标题"  align="center"width="400">
          <template slot-scope="scope">
						 <div>{{scope.row.title}}</div>
          </template>
        </el-table-column>
          <el-table-column label="文章来源" align="center" width="400">
              <template slot-scope="scope">
                      <div>{{scope.row.author}}</div>
              </template>
          </el-table-column>
        <el-table-column align="center" label="文章类型">
						<template slot-scope="scope">
               {{scope.row.type === 1?'系统公告':'常见问题'}}
            </template>
		</el-table-column>
        <el-table-column align="center" label="操作" width="200">
          <template slot-scope="scope">
            <el-button @click="edit(scope.row.id)" type="text" size="small">编辑</el-button>
            <el-button @click="del(scope.row.id)" type="text" size="small">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
			
     <div style="padding:20px;text-align:center;">
            <el-pagination
            background
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
              :current-page="page"
              :page-sizes="[10, 20, 30, 40]"
              :page-size="size"
              layout="total, sizes, prev, pager, next, jumper"
              :total="total">
            </el-pagination>
          </div>  

  </section>
</template>

<script>
export default {
  data() {
    return {
      dataList: [],
      state: "",
			name: "",
			total:0,
      page: 1,
      size: 10,
      dataList: [],
      addDiv:false,
      delDiv:false,
      title:'',
      type:'',
      id:'',
      amount:0
    };
  },
  methods: {
    //获取列表
    shousuo: function() {
      let that = this;
      this.axios({
        method: "GET",
        headers: {
          "Content-Type": "application/x-www-form-urlencoded"
        },
        url: "/notice?page="+(that.page-1)+"&size="+that.size+"&title="+that.title+"&type="+that.type
      }).then(function(response) {
        if (response.data.code == 200) {
					that.dataList = response.data.data.list;
					that.total=response.data.data.totalCount;
        }
      });
    },
	edit(id){
			this.$router.push({ 
        path:'/article',
        query: {
            id:id
        }
      })
    },

    // 删除
    del(id) {
			 let that=this;
			 this.$confirm('确定要删除此文章吗, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
						that.axios({
						url: "/giftmall/notice/"+id,
						method: "Delete",
						headers: {
							"Content-Type": "application/x-www-form-urlencoded"
						}
					})
						.then(response => {
							if (response.data.code ==200) {
								that.$message({
									message: "删除成功",
									type: "success"
								});
								that.shousuo();
							} 
						})
					
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });          
				});
			
    },

		handleSizeChange(val) {
        console.log(`每页 ${val} 条`);
        this.size=val;
        this.shousuo();
      },
      handleCurrentChange(val) {
        console.log(`当前页: ${val}`);
        this.page=val;
        this.shousuo();
      }
    
  },

  created() {
    this.shousuo();
  }
};
</script>
<style>
</style>




