<template>
    <el-dialog title="提示" :visible="true" width="30%" :show-close="false">
        <div class="list">
            <div class="name">余额:</div>
            <div class="value">{{totalMoney}}</div>
        </div>
        <div class="list">
            <div class="name">累计消费:</div>
            <div class="value">{{costMoney}}</div>
        </div>
        <div class="list">
            <div class="name">累计充值:</div>
            <div class="value">{{rechargeMoney}}</div>
        </div>
        <div class="list level">
            <div class="name">设置用户等级:</div>
            <el-select v-model="vip" placeholder="请选择" @change="changeDown">
                <el-option label="设置为普通用户" value="0"></el-option>
                <el-option label="设置为vip用户" value="1"></el-option>
                <el-option label="设置为超级vip用户" value="2"></el-option>
            </el-select>
        </div>        
        <el-row :span="24" :style="{ marginTop: 15+'px'}" v-show="isShowForm">
            <el-row :span="24">
                <el-col :span="8" >
                    <div class="demo-input-suffix" style="text-align:left">
                        <span>0.5kg(非偏远地区)：</span>
                        <el-input
                            :style="{width:150+'px'}"
                            placeholder="请输入内容"
                            prefix-icon="el-icon-search"
                            v-model="superVipForm.nearPrice1"                            
                        ></el-input>
                    </div>
                </el-col>
                <el-col :span="8">
                    <div class="demo-input-suffix" style="text-align:left">
                        <span>0.5-1kg(非偏远地区)：</span>
                        <el-input
                            :style="{width:150+'px'}"
                            placeholder="请输入内容"
                            prefix-icon="el-icon-search"
                            v-model="superVipForm.nearPrice2"
                            align="right"                            
                        ></el-input>
                    </div>
                </el-col>
                <el-col :span="8">
                    <div class="demo-input-suffix" style="text-align:left">
                        <span>续重(非偏远地区)：</span>
                        <el-input
                            :style="{width:150+'px'}"
                            placeholder="请输入内容"
                            prefix-icon="el-icon-search"
                            v-model="superVipForm.nearPrice3"                            
                        ></el-input>
                    </div>
                </el-col>
            </el-row>
            <el-row :span="24" :style="{marginTop:10+'px'}">
                <el-col :span="8">
                    <div class="demo-input-suffix" style="text-align:left">
                        <span :style="{}">0.5kg(偏远地区)：</span>
                        <el-input
                            :style="{width:150+'px'}"
                            placeholder="请输入内容"
                            prefix-icon="el-icon-search"
                            v-model="superVipForm.farPrice1"                            
                        ></el-input>
                    </div>
                </el-col>
                <el-col :span="8">
                    <div class="demo-input-suffix" style="text-align:left">
                        <span >0.5-1kg(偏远地区)：</span>
                        <el-input
                            :style="{width:150+'px'}"
                            placeholder="请输入内容"
                            prefix-icon="el-icon-search"
                            v-model="superVipForm.farPrice2"                            
                        ></el-input>
                    </div>
                </el-col>
                <el-col :span="8">
                    <div class="demo-input-suffix" style="text-align:left">
                        <span :style="{}">续重(偏远地区)：</span>
                        <el-input
                            :style="{width:150+'px'}"
                            placeholder="请输入内容"
                            prefix-icon="el-icon-search"
                            v-model="superVipForm.farPrice3"                            
                        ></el-input>
                        </div>
                </el-col>
            </el-row>
        </el-row>
        <span slot="footer" class="dialog-footer">
            <el-button @click="cancel">取 消</el-button>
            <el-button type="primary" @click="confirm">确 定</el-button>
        </span>
    </el-dialog>
</template>

<script>
export default {
    props:['data'],
    data() {
        return {
            vip: '',
            totalMoney: '', //余额
            costMoney: '', //累计消费
            rechargeMoney: '', //累计充值
            isShowForm:'', //用户等级
            superVipForm: {
                nearPrice1: "",
                nearPrice2: "",
                nearPrice3: "",
                farPrice1: "",
                farPrice2: "",
                farPrice3: ""
            }
        };
    },
    methods: {
        changeDown() {
            let that = this;
            if (this.vip == 2) {
                that.isShowForm = true;
            } else {
                that.isShowForm = false;
            }
        },
        cancel(){
            this.$emit('cancel', '')
        },
        confirm(){
            let that = this;
            this.axios.put(
                "/giftmall/order",
                this.qs.stringify({ id: this.data.id, remark: this.remark }),
                {
                    headers: {
                        "Content-Type": "application/x-www-form-urlencoded"
                    }
                }
            )
            .then(function(res) {
                if (res.data.code == 200) {
                    that.$emit('confirm', '')
                } 
            });
        },
        confirm() {
            let that = this;
            let config = {
                headers: {
                    "Content-Type": "multipart/form-data"
                }
            };
            let params = new FormData();
            params.append("userId", this.data.id);
            params.append("vip", this.vip);
            params.append("farPrice1", this.superVipForm.farPrice1 ? Math.round(this.superVipForm.farPrice1 * 100) : 0);
            params.append("farPrice2", this.superVipForm.farPrice2 ? Math.round(this.superVipForm.farPrice2 * 100 ): 0);
            params.append("farPrice3", this.superVipForm.farPrice3 ? Math.round(this.superVipForm.farPrice3 * 100) : 0);
            params.append("nearPrice1", this.superVipForm.nearPrice1 ? Math.round(this.superVipForm.nearPrice1 * 100) : 0);
            params.append("nearPrice2", this.superVipForm.nearPrice2 ? Math.round(this.superVipForm.nearPrice2 * 100 ): 0);
            params.append("nearPrice3", this.superVipForm.nearPrice3 ? Math.round(this.superVipForm.nearPrice3 * 100 ): 0);
            this.axios.put("/user/vip", params, config).then(res => {
                if (res.data.code == 200) {
                    that.$emit('confirm', '')
                }
            });
        },
        shousuo: function() {
            let that = this;
            this.axios({
                method: "GET",
                headers: {
                    "Content-Type": "application/x-www-form-urlencoded"
                },
                url:"/user/" + that.data.id
            }).then(function(res) {
                res = res.data;
                if (res.code == 200) {
                    that.dataInit(res.data);
                }   
            });
        },
        dataInit: function(res) {
            let data = this.data;
            this.vip = data.vip.toString();
            if(data.vip == 2) this.isShowForm = true;
            this.superVipForm = {
                nearPrice1: data.nearPrice1 / 100,
                nearPrice2: data.nearPrice2 / 100,
                nearPrice3: data.nearPrice3 / 100,
                farPrice1: data.farPrice1 / 100,
                farPrice2: data.farPrice2 / 100,
                farPrice3: data.farPrice3 / 100
            }
            this.totalMoney = res.totalMoney / 100;
            this.costMoney = res.costMoney / 100;
            this.rechargeMoney = res.rechargeMoney / 100;
        }
    },
    created() {
        this.shousuo();
    },
};
</script>

<style scoped lang="scss">
    .list{
        display: flex;
        margin-bottom: 10px;
        align-items: center;
        .name{
            margin-right: 20px;
            min-width: 70px;
            text-align: right;
        }
        &.level{
            margin-top: 20px;
            margin-bottom: 30px;
        }
    }
</style>



