<template>
  <section>
    <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
      <el-form :inline="true">
        <el-form-item
          label="分站名称"
          style="margin:10px 0 0  50px; width:25%; float:left; white-space:nowrap;"
        >
          <el-input v-model="webSiteName"></el-input>
        </el-form-item>

        <el-form-item
          label="会员账号"
          style="margin:10px 0  0 50px; width:25%; float:left; white-space:nowrap;"
        >
          <el-input v-model="account"></el-input>
        </el-form-item>

        <el-form-item
          label="时间"
          style="margin:10px 0 0 50px; float:left;; white-space:nowrap;"
        >
          <el-date-picker
            v-model="params.value6"
            type="daterange"
            range-separator="至"
            start-placeholder="开始日期"
            end-placeholder="结束日期"
          ></el-date-picker>
        </el-form-item>

        <div id="wrap">
          <el-select
            @change="onChange"
            :style="{paddingLeft:118+'px',marginTop:10+'px'}"
            v-model="value"
            placeholder="请选择消费类型"
            id="input"
          >
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>

          <el-form-item style="margin:10px 0 0 120px">
            <el-button type="primary" v-on:click="search">搜索</el-button>
            <el-button type="primary">导出</el-button>
          </el-form-item>
        </div>
      </el-form>
    </el-col>

    <!--列表-->
    <template>
      <el-table :data="users" highlight-current-row style="width: 100%;">
        <el-table-column type="index" width="60"></el-table-column>
        <el-table-column prop="webSiteName" label="分站名称" sortable></el-table-column>
        <el-table-column prop="userAccount" label="会员账号" sortable></el-table-column>
        <el-table-column label="金额" sortable>
          <template slot-scope="scope">
                {{scope.row.money/100}} 元
            </template>
        </el-table-column>
        <el-table-column prop="type" label="交易类型" sortable>
          <template slot-scope="scope">
                {{scope.row.type | type2text}}
            </template>
        </el-table-column>
        <el-table-column prop="remark" label="流水备注" sortable></el-table-column>
        <el-table-column :formatter="dateFormat" prop="createTime" label="时间" sortable></el-table-column>
       
      </el-table>
    </template>
    <div style="padding:20px;text-align:center;">
      <el-pagination
        background
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :page-sizes="[50, 100, 200, 300, 500]"
        :page-size="params.size"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
    </div>
  </section>
</template>

<script>
export default {
  data() {
    return {
      options: [
        {
          value: "1",
          label: "充值"
        },
        {
          value: "2",
          label: "消费"
        },
        {
          value: "3",
          label: "退费"
        },
        {
          value: "4",
          label: "提现"
        }
      ],
      value: "",
      users: [],
      webSiteName: "",
      account: "",
      params: {
        value6: "",
        page: 0,
        size: 50,
        type: ""
      },
      total: 0
    };
  },
  filters: {
    type2text(val) {
      const obj = {
        '1' : '充值',
        '2': ' 消费',
        '3': '退费',
        '4': '提现'
      }
      return obj[val]
    }
  },
  methods: {
    handleCurrentChange(val) {
      console.log(val);
      this.params.page = val - 1;
      this.showPage();
    },
    handleSizeChange(val) {
        console.log(`每页 ${val} 条`);
        this.params.size=val;
        this.showPage()
      },
    dateFormat: function(row, column) {
      var date = row[column.property];
      if (date == undefined) {
        return "";
      }
      return this.$moment(date).format("YYYY-MM-DD");
    },

    onChange(val) {
      console.log(val);
	  this.params.type = val;
	  this.showPage()
    },

    //发请求
    showPage() {
      var paramsData = this.params;
      for (var key in paramsData) {
        if (paramsData[key] === "") {
          delete paramsData[key];
        }
      }
      let { value6, page, size, type } = this.params;

      if (value6) {
        var begin = value6[0];
        var end = value6[1];
      }

      this.axios
        .get("/wallet", {
          params: {
            begin,
            end,
            page: page,
            size,
            type
          },
          headers: {
            "Content-Type": "application/x-www-form-urlencoded"
          }
        })
        .then(res => {
          console.log(res.data.data.list);
          this.users = res.data.data.list;
          this.total = res.data.data.totalCount;
        });
    },
    search() {
      this.showPage();
    }
  },
  mounted() {
    this.showPage();
  }
};
</script>
<style scope>

#input > .el-input__inner {
  width: 192px;
}
#wrap{
	/* text-align: center */
}
</style>



