<!--  -->
<template>
	<div class="page">
		<div class="title">
			<el-breadcrumb separator-class="el-icon-arrow-right">
				<el-breadcrumb-item>提现</el-breadcrumb-item>
			</el-breadcrumb>
		</div>
		<div class="body">
			<div class="applyFor" style="background:#fff">
				<div class="form">
				<div class="forTile">可提现金额{{userMoney}}元</div>
				<el-form ref="form" :model="form" label-width="80px" style="padding: 15px 0 25px 15px;">
					<h3>申请提现</h3>
					<el-form-item>
						<label class="el-form-item__label">
							<span class="name require">银行</span>
						</label>
						<el-input
							placeholder="例:中国建设银行"
							v-model="form.bank"
							maxlength="10"
							label-width="80px"
						/>
					</el-form-item>
					<el-form-item>
						<label class="el-form-item__label">
							<span class="name require">开户地</span>
						</label>
						<el-input
							placeholder="请输入开户省市"
							onbeforepaste="clipboardData.setData('text',clipboardData.getData('text').replace(/[\d]/g,''))"
							maxlength="10"
							v-model="form.site"
							label-width="80px"
						/>
					</el-form-item>
					<el-row>
						<el-form-item>
							<label class="el-form-item__label">
								<span class="name require">开户省市</span>
							</label>
							<el-input
								placeholder="省"
								v-model="form.province"
								label-width="80px"
								style="width:160px"
							/>
							&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
							<el-input
								placeholder="市"
								v-model="form.city"
								label-width="80px"
								style="width:160px"
							/>
						</el-form-item>
					</el-row>
					<el-form-item>
						<label class="el-form-item__label">
							<span class="name require">银行卡号</span>
						</label>
						<el-input
							placeholder="请输入卡号"
							oninput="value=value.replace(/[^\d]/g,'')"
							v-model="form.cardNum"
							label-width="80px"
						/>
					</el-form-item>
					<el-form-item>
						<label class="el-form-item__label">
							<span class="name require">姓名</span>
						</label>
						<el-input
							placeholder="请输入"
							v-model="form.name"
							label-width="80px"
						/>
					</el-form-item>
					<el-form-item>
						<label class="el-form-item__label">
							<span class="name require">申请金额</span>
						</label>					
						<el-input
							v-model="form.Monery"
							placeholder="不能低于100元"
							label-width="80px"
						/>
					</el-form-item>
					<el-form-item>
						<el-button @click="onSubmit">发起申请</el-button>
					</el-form-item>
					<div class="text">备注：提取金额为上一个自然月内金额,时间每月的10号且仅限一次</div>
				</el-form>
				</div>
				<div class="line"></div>
				<div class="record">
					<h3 class="recordTitle">提现记录</h3>
					<div class="table">
						<el-table
							:header-cell-style="{background:'#000',color:'#fff'}"
							:data="tableData"
							style="width: 100%"
						>
							<el-table-column prop="bank" label="提现银行" align="center" />
							<el-table-column prop="cardNum" label="银行卡号" align="center" />
							<el-table-column prop="userName" label="姓名" align="center" />
							<el-table-column prop="money" label="金额" align="center">
								<template slot-scope="scope">{{scope.row.money/100}}元</template>
							</el-table-column>
							<el-table-column prop="state" label="状态" align="center" />
						</el-table>
						<el-pagination
							class="pagination"
							@current-change="handleCurrentChange"
							:page-size="size"
							layout="prev, pager, next, jumper"
							background
							:total="total"
						/>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import { mapState } from "vuex";
const token = window.localStorage.getItem("token");
export default {
  	components: {},
	data() {
		return {
			form: {
				bank: "", //银行
				site: "", //开户地址 开户支行
				name: "", //姓名
				cardNum: "", //银行卡号
				Monery: "", //申请金额
				province: "", //省
				city: "", //市
			},
			userMoney: "", //可提现金额	  
			tableData: [],
			page: 0,
			size: 10,
			total: 0
		};
	},
	methods: {
		handleCurrentChange(val) {
			this.page = val - 1;
			this.query();
		},
		getMoney() {
			this.$axios.get("/webSite/adminStatistics", {
				headers: { "Content-type": "application/x-www-form-urlencoded; charset=utf-8" }
			})
			.then(res => {
				this.userMoney = res.data.data.totalMoney/100;
				this.$store.dispatch('workInfo',this.userMoney);			  
			});
		},
		//查询请求
		query() {
			this.$axios.get("/giftmall/sysDrawMoney", {
				headers: { "Content-type": "application/x-www-form-urlencoded; charset=utf-8" },
				params: {
					page: this.page,
					size: this.size
				}
			})
			.then(res => {
				this.tableData = res.data.data.list;
				this.total = res.data.data.totalCount;
			});
		},
		//发请求
		showPage() {
			let form = this.form;
			for (var key in form) {
				if (form[key] === "") {
					delete form[key];
				}
			}
			let { bank, cardNum, Monery, province, city } = this.form;
			if (Monery === 0) {
				this.$message({
					message: "无法提现",
					type: "warning"
				});
			} else if (Monery < 100) {
				this.$message({
					message: "不能低于100元",
					type: "warning"
				});
			} else {
				this.$axios({
					method: "post",
					url: "/giftmall/sysDrawMoney",
					data: this.$Qs.stringify({
						bank,
						cardNum,
						money: Monery * 100,
						province,
						city
					}),
					headers: {
						"Content-type": "application/x-www-form-urlencoded; charset=utf-8"
					}
				}).then(res => {
					this.query();
					this.getMoney();
				});
			}
		},
		onSubmit() {
			if (window.localStorage.getItem("token")) {
				let { bank, cardNum, Monery, province, city } = this.form;
				if (bank && cardNum && Monery && province && city) {
					this.showPage();
				} else {
					this.$message({
						message: "全部都要填哦",
						type: "warning"
					});
				}
			} else {
				this.$message({
					message: "您还没有登录哦,请登录后再试",
					type: "warning"
				});

				// 跳转回登录页面
				this.$router.push({ path: "/Login" });
			}
		}
	},
	mounted() {
		this.query();
		this.getMoney();
	}
};
</script>
<style lang='scss' scoped>
.page {
	border: 1px solid #ccc;
	.body {
		position: relative;
		.record {
			position: absolute;
			right: 20px;
			top: 4%;
			width: 50%;
			.recordTitle {
				text-align: center;
			}
			.table {
				margin-top: 20px;
			}
		}
	}

	.title {
		height: 40px;
		line-height: 40px;
		background: #eee;
		border-bottom: 1px solid #ccc;
	}

	.form {
		width: 45%;
		padding: 10px 10px 40px 10px;
		.forTile {
			width: 95%;
			padding-left: 10px;
			border-bottom: 1px solid #ccc;
			height: 40px;
			line-height: 40px;
		}
		h3 {
			text-align: center;
			height: 50px;
			line-height: 50px;
		}
		.el-input {
			width: 350px;
		}
		.region {
			width: 168px;
		}
		.text {
			text-align: center;
			color: #ccc;
		}
	}
}

.el-breadcrumb {
  height: 40px;
  line-height: 40px;
  margin-left: 35px;
}

.el-select {
  width: 163px;
}

.el-form-item__content {
  margin-left: 0;
}

.pagination {
  margin-top: 20px;
  text-align: center;
}

.applyFor {
  position: relative;

  .line {
    position: absolute;
    top: 4%;
    left: 46.5%;
    display: inline-block;
    width: 1px;
    background: #ccc;
    height: 90%;
  }
}
</style>
<style lang='scss' >
	.applyFor{
		.el-form-item{
			.el-form-item__content{
				margin-left: 0 !important;
			}
			.el-form-item__label{
				width: 80px;
				.name{
					display: inline-block;
				}
				.require{
					position: relative;
					&:before{
						content: '*';
						color: red;
						position: absolute;
						left: -10px;
						top: 0;
					}
				}
			}
		}
	}
</style>


