<template>
  <section>
    <el-form class="toolbar" label-width="100px" style="margin-top:10px;padding-top:20px;">
      <el-row>
        <el-col :span="6">
          <el-form-item label="账号">
            <el-input v-model="account"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item label="qq">
            <el-input v-model="qq"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item label="微信">
            <el-input v-model="wx"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item label="邀请人账号">
            <el-input v-model="inviteAccount"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item label="偏远预警">
            <el-select style="width:100%" v-model="farPlace" placeholder="请选择">
              <el-option label="全部" value=""></el-option>
              <el-option label="1%" value="1"></el-option>
              <el-option label="2%" value="2"></el-option>
              <el-option label="2%" value="3"></el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item>
            <el-button type="primary" @click="shousuo">搜索</el-button>
          </el-form-item>
        </el-col>
      </el-row>
    </el-form>
    <el-table
      highlight-current-row
      :data="dataList"      
      style="width: 100%;"
      border
    >
        <el-table-column align="center" resizable prop="webSiteName" label="分站名称"></el-table-column>
        <el-table-column align="center" resizable prop="account" label="账号"></el-table-column>
        <el-table-column align="center" resizable prop="farPlace" label="偏远预警"></el-table-column>
        <el-table-column align="center" resizable prop="inviteCode" label="邀请人账号"></el-table-column>
        <el-table-column align="center" resizable prop="qq" label="qq"></el-table-column>
        <el-table-column align="center" resizable prop="wx" label="微信"></el-table-column>
        <el-table-column align="center" resizable prop="vip2" label="用户等级"></el-table-column>
        <el-table-column align="center" resizable prop="createTime" width="150" :formatter="dateFormat" label="注册时间"></el-table-column>
        <el-table-column align="center" resizable prop="noLoginDate" label="未登录天数"></el-table-column>
        <el-table-column align="center" resizable prop="activate" :formatter="activateFormat" label="激活状态"></el-table-column>
        <el-table-column align="center" resizable prop="totalMoney" :formatter="moneyFormat" label="余额（元）"></el-table-column>
        <el-table-column align="center" resizable prop="totalRechargeMoney" :formatter="moneyFormat" label="累计充值（元）"></el-table-column>
        <el-table-column align="center" resizable prop="totalPackCount"  label="累计单量"></el-table-column>
        <el-table-column resizable align="center" label="操作">
            <template slot-scope="scope">
                <el-button type="small" @click='editClick(scope.row)'>更多</el-button>
            </template>
        </el-table-column>
    </el-table>
    <div style="padding:20px;text-align:center;">
      <el-pagination
        background
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="page"
        :page-sizes="[10, 20, 30, 40]"
        :page-size="size"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
    </div>
    <subscriber1Dia 
        v-if='editState' :data="rowData" 
        @cancel="editCancle"
        @confirm="editConfrim"
    />
  </section>
</template>

<script>
import subscriber1Dia from './subscriber1Dia';
export default {
  data() {
    return {
        total: 0,
        page: 1,
        size: 10,
        dataList: [],
        account: "",
        qq: "",
        wx: "",
        inviteAccount: "",
        farPlace: "",
        dialogVisible: false,
        value: "",
        currentIndex: 0, //点击表格的当前行数
        row: {},
        isShowForm: false,
        editState: false,
        rowData: null, //当前点击的row
    };
  },
  components:{
    subscriber1Dia
  },
  methods: {
    dateFormat(row, column) {
      var date = row[column.property];
      if (date === undefined) {
        return "";
      }
      return this.$moment(date).format("YYYY-MM-DD HH:mm:ss");
    },
    activateFormat(row, column) {
      var activate = row[column.property];
      return activate ? '已激活' : '未激活';
    },
    moneyFormat(row, column) {
      var money = row[column.property];
      if (money === undefined) {
        return 0;
      }
      return money / 100;
    },
    weightFormat(row, column) {
      var weight = row[column.property];
      if (weight === undefined) {
        return 0;
      }
      return weight / 1000;
    },
    editClick(row){
        this.rowData = row;
        this.editState = !this.editState;
    },
    editCancle(){
        this.editState = false
    },
    editConfrim(){
        this.shousuo();
        this.editCancle();
    },
    shousuo: function() {
      let that = this;
      this.axios({
        method: "GET",
        headers: {
          "Content-Type": "application/x-www-form-urlencoded"
        },
        url:
          "/user?page=" +
          (that.page - 1) +
          "&size=" +
          that.size +
          "&account=" +
          that.account +
          "&qq=" +
          that.qq +
          "&wx=" +
          that.wx +
          "&inviteAccount=" +
          that.inviteAccount +
          "&farPlace=" +
          that.farPlace
      }).then(function(res) {
        if (res.data.code == 200) {
          that.dataList = res.data.data.list;

          that.total = res.data.data.totalCount;

          that.dataList.forEach(item => {
            if (item.vip == 0) {
              item.vip2 = "普通";
            } else if (item.vip == 1) {
              item.vip2 = "vip";
            } else if (item.vip == 2) {
              item.vip2 = "超级vip";
            }
          });
        }
      });
    },
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
      this.size = val;
      this.shousuo();
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
      this.page = val;
      this.shousuo();
    }
  },
  created() {
    this.shousuo();
  },
};
</script>



