<!--  -->
<template>
  <div class>
    <el-row>
      <el-col :span="24" class="top">
        <div class="title">
          <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item>流水账单</el-breadcrumb-item>
          </el-breadcrumb>
        </div>
        <div class="wrap">
          <div class="count">账户余额: 0</div>
          <div class="time">
            <span style="margin-right:15px;font-size:14px;color:#ccc;margin-left:5px">日期</span>
            <el-date-picker
              v-model="params.value1"
              type="daterange"
              value-format="yyyy-MM-dd"
              range-separator="至"
              start-placeholder="开始日期"
              end-placeholder="结束日期"
            ></el-date-picker>
            <el-button style="width:100px" @click="search">搜索</el-button>
          </div>
        </div>
      </el-col>
    </el-row>
    <el-table
      :data="tableData"
      style="width: 100%"
      :header-cell-style="{background:'#000',color:'#fff'}"
    >
      <el-table-column prop="createTime" label="日期" :formatter="dateFormat" align="center"></el-table-column>
      <el-table-column prop="remark" label="款项" :formatter="formatRole"  align="center"></el-table-column>
      <el-table-column prop="adminBonus" label="金额" align="center">
        <template slot-scope="scope"> 
            {{scope.row.adminBonus/100}}元
         </template>
      </el-table-column>
    </el-table>
    <el-pagination  
      class="pagination"
      background
      @current-change="handleCurrentChange"
      align="center"
      :page-size="this.params.size"
      layout="prev, pager, next, jumper"
      :total="total"
    ></el-pagination>
  </div>
</template>

<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';
const token = sessionStorage.getItem("token");

export default {
  //import引入的组件需要注入到对象中才能使用
  components: {},
  data() {
    return {
      total:0,
      pickerOptions: {
        shortcuts: [
          {
            text: "最近一周",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "最近一个月",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "最近三个月",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit("pick", [start, end]);
            }
          }
        ]
      },
      params:{
        value1: "",
        page:0,
        size:10
      },
      tableData: [
        
      ]
    };
  },
  //监听属性 类似于data概念
  computed: {},
  //监控data中的数据变化
  watch: {},
  //方法集合
  methods: {
    dateFormat(row, column) {
      var date = row[column.property];
      if (date === undefined) {
        return "";
      }
      return this.$moment(date).format("YYYY-MM-DD HH:mm:ss");
    },
     handleCurrentChange(val){
       this.params.page = val-1
       this.showPage()
     },
    showPage(){
      var params = this.params

      for (var key in params) {
          
          if (params[key] === "") {
           
            delete params[key];
          }
        }

      let {value1,page,size} = this.params
      if(value1){
        var begin = value1[0]
        var end = value1[1]
      }

      if(value1 || page || size){
        this.$axios.get('/income',{
        params:{
          begin,
          end,
          page,
          size
        },
        headers: {
                "Content-type":
                  "application/x-www-form-urlencoded; charset=utf-8"
              }
      }).then(res => {
        this.tableData = res.data.data.list
        this.total = res.data.data.totalCount
      })
      }
      
    },
    search(){

      if (sessionStorage.getItem("token")) {
        console.log("111");
        // this.formInline.page - 1
        let { value1 } = this.params;
        if (value1) {
          this.showPage()
        } else {
          this.$message({
            message: "至少一个查询条件",
            type: "warning"
          });
        }  
      }else{
        this.$message({
          message: "您还没有登录哦,请登录后再试",
          type: "warning"
        });

        // 跳转回登录页面
        this.$router.push({ path: "/Login" });
      }
    },
    cellStyle() {
      return "text-align:center";
    },
    rowClass() {
      return "text-align:center";
    },
    formatRole(row, column) {
      let name = row[column.property];
      let n = "" + name;
      return n;
    }
  },
  //生命周期 - 创建完成（可以访问当前this实例）
  created() {},
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted() {
    this.showPage()
  },
  beforeCreate() {}, //生命周期 - 创建之前
  beforeMount() {}, //生命周期 - 挂载之前
  beforeUpdate() {}, //生命周期 - 更新之前
  updated() {}, //生命周期 - 更新之后
  beforeDestroy() {}, //生命周期 - 销毁之前
  destroyed() {}, //生命周期 - 销毁完成
  activated() {} //如果页面有keep-alive缓存功能，这个函数会触发
};
</script>
<style lang='scss' scoped>
.top {
  width: 100%;
  height: 150px;
  background: pink;

  .title {
    background: #eee;
    border: 1px solid #ccc;
    border-bottom: 0px;
  }

  .wrap {
    background: #fff;
    border: 1px solid #ccc;
    height: 115px;

    .count, .time {
      margin-top: 15px;
      padding-left: 20px;
    }
  }
}

.el-table {
  border: 1px solid #ccc;
  margin-top: 15px;
}
.el-breadcrumb {
  height: 40px;
  line-height: 40px;
  margin-left: 35px;

}
.pagination {
  margin-top:20px;
  text-align:center;
}
</style>


