<template>
  <section>
    <!-- <el-form class="toolbar" label-width="100px" style="margin-top:10px;padding-top:20px;">
      <el-row>
        <el-col :span="6">
          <el-form-item label="账号">
            <el-input v-model="account"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item label="qq">
            <el-input v-model="qq"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item label="微信">
            <el-input v-model="wx"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item label="邀请人账号">
            <el-input v-model="inviteAccount"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item label="偏远预警">
            <el-select style="width:100%" v-model="farPlace" placeholder="请选择">
              <el-option label="1%" value="1"></el-option>
              <el-option label="2%" value="2"></el-option>
              <el-option label="2%" value="3"></el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="6">
          <el-form-item>
            <el-button type="primary" @click="shousuo">搜索</el-button>
          </el-form-item>
        </el-col>
      </el-row>
    </el-form> -->
    <el-table
      highlight-current-row
      :data="dataList"      
      style="width: 100%;"
    >
        <el-table-column align="center" prop="webSiteName" label="分站名称"></el-table-column>
        <el-table-column align="center" prop="account" label="账号"></el-table-column>
        <el-table-column align="center" prop="taskId" label="订单ID"></el-table-column>
        <el-table-column align="center" prop="money" label="任务金额">
            <template slot-scope="scope">
                {{scope.row.money/100}} 元
            </template>
        </el-table-column>
        <el-table-column align="center" prop="orignal" label="来源"></el-table-column>
        <el-table-column align="center" prop="stateName" label="状态"></el-table-column>
        <el-table-column align="center" prop="createTime" label="申请退货时间"></el-table-column>
        <el-table-column width="200px" align="center" label="操作">
            <template slot-scope="scope">
                <el-button type="small" @click='agreeRow(scope.row)'>同意</el-button>
                <el-button type="small" @click='rejectRow(scope.row)'>拒绝</el-button>
                <el-button type="small" @click='deleteRow(scope.row)'>删除</el-button>
            </template>
        </el-table-column>
    </el-table>
    <div style="padding:20px;text-align:center;">
      <el-pagination
        background
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="page"
        :page-sizes="[10, 20, 30, 40]"
        :page-size="size"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
    </div>

    <el-dialog
        title="提示"
        :visible.sync="dialogVisible"
        width="30%"
        :before-close="handleClose">
        <span>{{msg}}</span>
        <span slot="footer" class="dialog-footer">
            <el-button @click="dialogVisible = false">取 消</el-button>
            <el-button type="primary" @click="dialogSure">确 定</el-button>
        </span>
    </el-dialog>
  </section>
</template>

<script>
import {gettime} from "../../utils/formData";  
const tips = ['是否确认该操作?']
export default {
  data() {
    return {
        total: 0,
        page: 1,
        size: 10,
        dataList: [],
        account: "",
        qq: "",
        wx: "",
        msg:'',
        inviteAccount: "",
        farPlace: "",
        dialogVisible: false,
        scopeRow: null,
        type:'', //操作类型
        value: "",
        currentIndex: 0, //点击表格的当前行数
        row: {},
        isShowForm: false,
        editState: false,
        rowData: null, //当前点击的row
    };
  },
  methods: {
    editClick(row){
        this.rowData = row;
        this.editState = !this.editState;
    },
    editCancle(){
        this.editState = false
    },
    editConfrim(){
        this.shousuo();
        this.editCancle();
    },
    dialogSure(){
        this.dialogVisible = false;
        this.handleAjax();
    },
    handleAjax(){
        let that = this;
        if(this.type === 'delete') return this.deleteAjax();
        let params = new FormData();
            params.append('id', this.scopeRow.id)
        if(this.type === 'add'){
            params.append('state', 1)
        }else if(this.type === 'reject'){
            params.append('state', -1)
        }
        this.axios.put('/tui',  params, {
            headers: {
                'Content-Type':'application/x-www-form-urlencoded'
            }
        }).then(function (res) {
            if(res.data.code == 200){ 
                that.$message({
                    message:'操作成功',
                    type: 'success'
                });  
                that.shousuo();
            }else{
                that.$message({
                    message: res.data.message,
                    type: 'error'
                });
            }
        })
    },
    deleteAjax(){
        let that = this;
        that.axios({
            url: "/tui/" + this.scopeRow.id,
            method: "Delete",
            headers: {
                "Content-Type": "application/x-www-form-urlencoded"
            }
        })
        .then(res => {
            if (res.data.code ==200) {
                that.$message({
                    message: "删除成功",
                    type: "success"
                });
                that.shousuo();
            }else{
                that.$message({
                    message: res.data.message,
                    type: 'error'
                });
            }
        })
    },
    agreeRow(row){
        this.scopeRow = row;
        if(row.state != 0) return;
        this.msg = tips[0];
        this.type = 'add';
        this.dialogVisible = true;
    },
    rejectRow(row){
        this.scopeRow = row;
        if(row.state != 0) return;
        this.msg = tips[0];
        this.type = 'reject';
        this.dialogVisible = true;
    },
    deleteRow(row){
        this.scopeRow = row;
        this.msg = tips[0];
        this.type = 'delete';
        this.dialogVisible = true;
    },
    shousuo: function() {
      let that = this;
      this.axios({
        method: "GET",
        headers: {
          "Content-Type": "application/x-www-form-urlencoded"
        },
        url:
          "/tui?page=" +
          (that.page - 1) +
          "&size=" +
          that.size +
          "&account=" +
          that.account +
          "&qq=" +
          that.qq +
          "&wx=" +
          that.wx +
          "&inviteAccount=" +
          that.inviteAccount +
          "&farPlace=" +
          that.farPlace
      }).then(function(res) {
        if (res.data.code == 200) {
            that.dataList = res.data.data.list;
            that.total = res.data.data.totalCount;
            for(var i=0;i<that.dataList.length;i++){
                that.dataList[i].createTime=gettime(that.dataList[i].createTime)
                if(that.dataList[i].state == 0){
                    that.dataList[i].stateName = '已申请';
                }else if(that.dataList[i].state == 1){
                    that.dataList[i].stateName = '已通过';
                }else if(that.dataList[i].state == -1){
                    that.dataList[i].stateName = '已拒绝';
                }
            }
        }
      });
    },
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
      this.size = val;
      this.shousuo();
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
      this.page = val;
      this.shousuo();
    }
  },
  created() {
    this.shousuo();
  },
};
</script>



